import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_INDUSTRYLINES_PAGE,
  API_INDUSTRYLINES_DEL,
  API_INDUSTRYLINES_CREATE,
  API_INDUSTRYLINES_EXPORT,
  API_INDUSTRYLINES_UPDATE
} from 'api'
//同步
export const update = object => ({
  type: actionType.INDUSTRYLINE_UPDATE,
  data: object
})

//异步
// 获取列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.INDUSTRYLINE_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_INDUSTRYLINES_PAGE, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page, size } = reqData.searchParams
      dispatch({
        type: actionType.INDUSTRYLINE_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          searchResult: data.result.list || []
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.INDUSTRYLINE_FAIL
      })
    })
}
// 删除 - 商品属性数据
export const handleOperator = (reqData, callBack) => dispatch => {
  getData({ api: API_INDUSTRYLINES_DEL, ...reqData })
    .then(data => {
      callBack && callBack(data)
    })
    .catch(err => {
      dispatch({
        type: actionType.INDUSTRYLINES_FAIL
      })
    })
}
// 创建/编辑仓库
export const handleAddOrEdit = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.INDUSTRYLINE_UPDATE,
    data: { modalTuttonLoading: true }
  })
  let api =
    reqData.data && reqData.data.id
      ? API_INDUSTRYLINES_UPDATE
      : API_INDUSTRYLINES_CREATE
  getData({ api, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.INDUSTRYLINE_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.INDUSTRYLINE_FAIL
      })
    })
}
// 导出
export const handleExport = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.INDUSTRYLINE_UPDATE,
    data: { modalTuttonLoading_handleExport: true }
  })
  getData({ api: API_INDUSTRYLINES_EXPORT, ...reqData })
    .then(data => {
      callBack && callBack()
      dispatch({
        type: actionType.INDUSTRYLINE_UPDATE,
        data: { modalTuttonLoading_handleExport: false }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.INDUSTRYLINE_UPDATE,
        data: { modalTuttonLoading_handleExport: false }
      })
    })
}



