import { Bread } from 'Bread'
import React, { PureComponent } from 'react'
import Form from './components/./Form'
import Table from './components/./Table'
import Tool from './components/./Tool'
import { connect } from 'react-redux'
import { mapDispatchToProps } from './redux/mapDispatchToProps'

@connect(
  state => ({
    ...state.industryLine,
    menuPermissionIds: state.menuPermissionIds
  }),
  mapDispatchToProps
)
export default class App extends PureComponent {
  constructor(props) {
    super(props)
  }
  componentWillUnmount() {
    this.props.update({
      searchResult: [],
      searchParams: {},
      productCodeOptions: {}
    })
  }
  componentWillReceiveProps(nextProps) {
    const { location, handleGetList, searchParams, page, size } = this.props
    if (location != nextProps.location) {
      handleGetList({ searchParams: { page, size, ...searchParams } })
    }
  }
  render() {
    const { history, menuPermissionIds } = this.props
    return (
      <section>
        <Bread route={['设置中心', '商品管理', '工艺路线设置']} />
        <Form />
        <Tool history={history} menuPermissionIds ={menuPermissionIds}/>
        <Table history={history} menuPermissionIds ={menuPermissionIds} />
      </section>
    )
  }
}



