import React, { PureComponent } from 'react'
import { message, Modal, Button } from 'antd'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { API_INDUSTRYLINES_IMPORT } from 'api'
import { Import } from 'HOC'
const confirm = Modal.confirm
@connect(
  state => ({ ...state.industryLine }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  // 新增
  handleAdd = () => {
    const { history } = this.props
    history.push('/mms/settingCenter/productManage/industryLine/create?editOrCreate=create&permissionFlag=allowEdit')
  }
  /* 下载模版 */
  handleDownLoad = key => {
    const downloadData = JSON.parse(localStorage.getItem('b2bMmsDownload'))
    const downLoadObj = (downloadData && downloadData[key]) || ''
    if (!downLoadObj) {
      return message.error('模板下载地址有误')
    }
    location.href = `${downloadData[key].downloadUrl}`
  }
  handleOperator = () => {
    const {
      handleGetList,
      searchParams,
      page,
      size,
      handleOperator,
      selectedRowKeys,
      update
    } = this.props
    if (!(selectedRowKeys && selectedRowKeys.length > 0)) {
      return message.error('没有可以操作的选项')
    }
    confirm({
      title: '温馨提示',
      content: '是否进行删除操作？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleOperator(
          {
            data: selectedRowKeys
          },
          () => {
            update({ selectedRowKeys: [] })
            message.success('操作成功')
            handleGetList({ searchParams: { page, size, ...searchParams } })
          }
        )
      }
    })
  }
  // 导出
  handleExport = () => {
    const { handleExport, searchParams, page, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    handleExport({ searchParams: { page, size, ...reqObj } }, () => {
      message.success('导出成功')
    })
  }
  /* 导入成功 */
  handleImportSuccess = res => {
    const { handleGetList, searchParams, page, size, update } = this.props
    res && res.message === 'ok' && message.success('导入成功')
    update({
      tableLoading: false,
      modalTuttonLoading: false
    })
    handleGetList({ searchParams: { page, size, ...searchParams } })
  }

  /* 报错导出 */
  handleExprotError = data => {
    const { update } = this.props
    update({
      tableLoading: false,
      modalTuttonLoading: false
    })
  }
  render() {
    const { modalTuttonLoading, modalTuttonLoading_handleExport, menuPermissionIds = [] } = this.props
    return (
      <div className={style['table-tool']}>
        {menuPermissionIds && menuPermissionIds.includes('industryLine_add') && (
          <Button type='primary' className={style.btn} onClick={this.handleAdd}>
            新增
          </Button>
        )}
        {menuPermissionIds && menuPermissionIds.includes('industryLine_delete') && (
          <Button
            type='primary'
            className={style.btn}
            onClick={this.handleOperator}
          >
            删除
          </Button>
        )}

        <Button
          type='primary'
          className={style.btn}
          icon='sync'
          disabled={modalTuttonLoading_handleExport}
          onClick={() => {
            this.handleDownLoad('T03')
          }}
        >
          下载模板
        </Button>
        {menuPermissionIds && menuPermissionIds.includes('industryLine_import') && (
          <Import
            multiple={false}
            action={{ api: API_INDUSTRYLINES_IMPORT }}
            onOk={this.handleImportSuccess}
            onExprotError={this.handleExprotError}
            beforeImport={() => {
              const { update } = this.props
              update({
                tableLoading: true,
                modalTuttonLoading: true
              })
            }}
            onResponse={data => {}}
          >
            <Button
              type='primary'
              icon='upload'
              className={style.btn}
              loading={modalTuttonLoading}
              disabled={modalTuttonLoading_handleExport}
            >
              导入
            </Button>
          </Import>
        )}
        {menuPermissionIds && menuPermissionIds.includes('industryLine_export') && (
          <Button
            type='primary'
            className={style.btn}
            onClick={this.handleExport}
            loading={modalTuttonLoading_handleExport}
          >
            导出
          </Button>
        )}
      </div>
    )
  }
}



